import React from "react";
import { Provider } from "react-redux";
import { Route, BrowserRouter, Switch, Router } from "react-router-dom";

import { ConnectedRouter } from "connected-react-router";

import configureStore, { history } from "./configure-store";
import {
  MainContainer,
  ResultContainer,
  PayGuidePage,
  ForgotPasswordPage,
  ErrorPage
} from "./containers";
import packageJson from "../package.json";

const store = configureStore();

const publicPath = "/colossus/web/v1/h5payment/";

// export const routeCodes = {
//   HOME: publicPath,
//   FORGOT_PASSWORD: `${publicPath}forgot-password`,
//   RESULT: `${publicPath}result`,
//   ERROR_PAGE: `${publicPath}error-page`,
//   DETAIL_PAGE: `${publicPath}:paymentott`
// };
export const routeCodes = {
  // HOME: "/",
  FORGOT_PASSWORD: `/forgot-password`,
  RESULT: `/result`,
  ERROR_PAGE: `/error-page`,
  DETAIL_PAGE: `/:paymentott`
};

const MainApp = () => {
  console.log("location", window.location);
  console.log("process.env.PUBLIC_URL", process.env.PUBLIC_URL);
  console.log("process.env.HOME", process.env.HOME);

  return (
    <Provider store={store}>
      <div>
        <p
          style={{
            fontSize: 10,
            color: "gray"
          }}
        >
          {`version ${packageJson.version}`}
        </p>
      </div>
      <ConnectedRouter history={history}>
        <BrowserRouter basename={publicPath} history={history}>
          <Route exact path={routeCodes.RESULT} component={ResultContainer} />
          <Route exact path={routeCodes.HOME} component={PayGuidePage} />
          <Route
            exact
            path={routeCodes.FORGOT_PASSWORD}
            component={ForgotPasswordPage}
          />

          <Route exact path={routeCodes.ERROR_PAGE} component={ErrorPage} />
          <Route
            exact
            path={routeCodes.DETAIL_PAGE}
            component={MainContainer}
          />
          {/* <Route component={PayGuidePage} /> */}
        </BrowserRouter>
      </ConnectedRouter>
    </Provider>
  );
};

export default MainApp;
